.header {
    color: #fff;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60vh;
    .nav-bg {
        width: 100%;
        padding-bottom:10px;
        background: linear-gradient(rgba(0,0,0,0.6) , rgba(0,0,0,0));
        border-top: 4px solid red;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
    }
    .sticky{
        position: fixed;
        background: rgba(255, 255, 255, 0.76);
        transition: 0.4s all ease-in-out;
        a{
            color: #000;
        }
        i{
            color: #000;
            &:hover{
                color:red;
            }
        }
        .sandwich{
            .bread{
                background-color: #000;
            }
        }
    }
    .header-txt{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        h1{
            margin-top: 80px;
            font-size: 2.5em;
            letter-spacing: 6px;
            @media  screen and (max-width:790px) {
                margin: 20px; 
            }
        }
        p{
            color: #fff;
        }
        
    }
}
.hero {
    background: url(../img/master-slides-03.jpg) center/cover;
    height: 100vh;
    .header-txt{
        text-align: center;
        p{
            margin: 0;
            margin-top: 80px;
            font-size: 3em;
            letter-spacing: 2;
            text-transform: none;
        }
        h1{
            font-size: 6em;
            @media  screen and (max-width:790px) {
                font-size: 4em;
            }
        }
        
    }
}
.blog {
    background: url(../img/master-slides-05.jpg) center/cover;
}
.contact {
    background: url(../img/master-slides-06.jpg) center/cover;
}
